}

/* mobile specific stylizing */
@media screen and (max-width: 820px){
    .footer-links{
        padding-top: 2rem;
    }

    .footer-input{
        width: 100%;
    }

    .btn{
        width: 100%;
    }

    .footer-link-wrapper{
        flex-direction: column;
    }

    .social-media-wrap{
        flex-direction: column;
    }
    
    /* undo the fix on mobile so it displays centered */
    .social-logo{
        margin-right: 0px;
    }
}