video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container{
    /* background: url('../assets/images/img-2.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contains;
}

.hero-container > h1{
    color: #fff;
    margin-right: 60px;
    margin-left: 60px;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container > p{
    color: #fff;
    font-size: 32px;
    margin-top: 8px;
    margin-right: 60px;
    margin-left: 60px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

/*  ---- unused buttons on heroSection --- */
.hero-btns {
    margin-top: 32px;
}

.hero-btns .btn{
    margin-top: 6px;
}

/* for mobile screens */
@media screen and (max-width: 960px){
    .hero-container > h1{
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px){
    .hero-container > h1{
        font-size: 50px;
        margin-top: -100px;
    }

    .hero-container > p{
        font-size: 30px;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;
    }

    .btn{
        width: 100%;
    }
}